// FADE IN DOWN
[data-scroll-animation="fade-in-down"] {
	opacity: 0;
	transform: translateY($offset-animation);
	transition: opacity 0.6s $easing, transform 0.6s $easing;
	will-change: transform;

	.is-loader-complete & {

		&.is-inview {
			transform: none;
			opacity: 1;
		}

	}
}

// FADE IN UP
[data-scroll-animation="fade-in-up"] {
	opacity: 0;
	transform: translateY(-$offset-animation);
	transition: opacity 0.6s $easing, transform 0.6s $easing;
	will-change: transform;

	.is-loader-complete & {

		&.is-inview {
			transform: none;
			opacity: 1;
		}

	}

}

// FADE IN RIGHT
