// ZOOM IN
[data-scroll-animation="zoom-in"] {
	opacity: 0;
	transform: scale(2);
	transition: opacity 0.6s $easing, transform 2s $easing;
	will-change: transform;

	.is-loader-complete & {

		&.is-inview {
			transform: none;
			opacity: 1;
		}

	}

}

// FADE IN DOWN STAGGERED
[data-scroll-animation="fade-in-down-staggered"] {
	opacity: 0;
	transform: translateY($offset-animation);
	transition: opacity 0.6s $easing, transform 0.6s $easing;
	will-change: transform;

	.is-loader-complete & {

		&.is-inview {
			transform: none;
			opacity: 1;

			@for $i from 1 through 20 {
				&:nth-child(#{$i}){
					transition-delay: $i*0.15s;
				}
			}
		}

	}

}

