// ZOOM IN
[data-scroll-animation="zoom-in-staggered"] {

	figure {
		opacity: 0;
		transform: scale(1.4);
		transition: opacity 0.6s $easing, transform 0.6s $easing;
		will-change: transform;
	}

	.is-loader-complete & {

		&.is-inview {

			figure {
				transform: none;
				opacity: 1;
			}

			@for $i from 1 through 20 {
				&:nth-child(#{$i}){
					figure {
						transition-delay: $i*0.2s;
					}
				}
			}

		}

	}

}

// COMPARISON
[data-scroll-animation="comparison"] {

	.comparison__media {
		width: 100%;
		height: 100%;
	}

	.comparison__img {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
	}

	.comparison__after-media {
		position: absolute;
		overflow: hidden;
		top: 0;
		transform: translate(100%, 0px);

		.comparison__img {
			transform: translate(-100%, 0px);
		}
	}

}


// PIN HORIZONTAL
[data-scroll-animation="pin-horizontal"] {

	[data-scroll-animation-target] {
		position: relative;
		display: flex;
		white-space: nowrap;
		border: 1px solid $color-white;
	}

	.pin-horizontal__container {
		border: 1px dashed $color-white;
	}

	.pin-horizontal__item {
		display: inline-block;
		white-space: normal;
		vertical-align: top;
		width: 250px;
		@screen md {
			width: 500px;
		}
	}

}



// PIN VERTICAL
[data-scroll-animation="pin-vertical"] {
	position: relative;
	overflow: hidden;

	.pin-vertical__container {
		position: absolute;
		left: 0;
		right: 0;
	}

	.pin-vertical__container--left {
		top: 0;
	}
	.pin-vertical__container--center {
		bottom: 0;
	}
	.pin-vertical__container--right {
		top: 0;
	}

	.pin-vertical__item {
		position: relative;
		display: block;
		width: 100%;
		padding: ritmo(0.2);
		overflow: hidden;
		@screen md {
			padding: ritmo(2);
		}
	}

}



