.product-remove {

	padding: 0;
	position: relative;
	text-align: center;

	.remove {
		color: $color-grey;
		text-decoration: none;
		width: 20px;
		height: 20px;
		line-height: 15px;
		text-align: center;
		font-size: 20px;
		background: 0 0;
		border: 1px solid;
		border-radius: 50%;

		@include media('<=md') {
			position: absolute;
			right: -14px;
			top: -30px;
			z-index: 1;
		}

		&:hover {
			background-color: $color-primary;
		}
	}
}